.default {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #ed3330;
    padding: 15px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    transition: all 0.2s ease 0s;
    outline: none;
    margin:5px;
    cursor: pointer;
    letter-spacing: 2px;
}

.default:hover {
    background-color: #be0909;
    transition: all 0.2s ease 0s;
}

